import { Component, ReactNode, createElement } from "react";
import { View, TextStyle, ViewStyle, NativeEventEmitter, EmitterSubscription } from "react-native";
import { Style } from "@mendix/pluggable-widgets-tools";
import { KeyCaptureNativeProps } from "../typings/KeyCaptureNativeProps";
import Scanner from "barcodescanneruart";

export interface CustomStyle extends Style {
    container: ViewStyle;
    label: TextStyle;
}

export class KeyCaptureNative extends Component<KeyCaptureNativeProps<CustomStyle>> {
    subscription: EmitterSubscription | null = null;

    onScan = (event: string): void => {
        this.props.inputString.setValue(event);
        if (this.props.onScan && this.props.onScan.canExecute && !this.props.onScan.isExecuting) {
            this.props.onScan.execute();
        }
    };

    componentDidMount(): void {
        if (Scanner) {
            Scanner.startService(
                (err: string) => console.error(err),
                () => {
                    // empty
                }
            );
            this.subscription = new NativeEventEmitter().addListener("barcodeData", this.onScan);
        }
    }

    componentWillUnmount(): void {
        if (this.subscription) {
            this.subscription.remove();
        }
    }

    render(): ReactNode {
        return <View />;
    }
}
