import { NativeModules, DeviceEventEmitter } from "react-native";

const Scanner = NativeModules.Reader;

// Scanner.start = () => {
//     reader.startService((err) => {console.log("err: ", err)}, (msg) => {console.log("msg: ", msg)} );
// };

Scanner.on = (eventName, handler) => {
  if (!allowedEvents.includes(eventName)) {
    throw new Error(`Event name ${eventName} is not a supported event.`);
  }
  DeviceEventEmitter.addListener(eventName, handler);
};

Scanner.off = (eventName, handler) => {
  if (!allowedEvents.includes(eventName)) {
    throw new Error(`Event name ${eventName} is not a supported event.`);
  }
  DeviceEventEmitter.removeListener(eventName, handler);
};

module.exports = Scanner;


